body {
  width: 100vw;
  font-family: sans-serif;
  padding: 50px;
}

/* dog card styles */
.dog-card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: fit-content;
  text-align: left;
  border-radius: 1px;
}

.dog-card:hover {
  box-shadow: 0 14px 18px 0 rgba(0, 0, 0, 0.2),
    0 16px 30px 0 rgba(0, 0, 0, 0.19);
}

p {
  margin: 0;
}

.text-container {
  padding: 10px 16px 14px 16px;
}

.title {
  color: #0f5ef0;
  font-weight: 600;
  font-size: 15px;
  margin-bottom: 18px;
}

.subtitle {
  color: #7a8594;
  font-size: 12px;
  font-weight: 900;
  margin-bottom: 20px;
}

.info {
  color: #7a8594;
  font-size: 12px;
}
img {
  width: 290px;
}

/* Form Styles */

#form-section {
  text-align: left;
}

* {
  box-sizing: border-box;
  transition: 0.3s;
}

body {
  margin: 0;
}

section {
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  overflow: scroll;
  background-position: fixed;
  display: flex;
  justify-content: center;
}

.container {
  background-color: white;
  height: fit-content;
  padding: 50px 80px;
  margin: 50px;
  width: 100%;
  max-width: 600px;
}

h1 {
  font-size: 3.2rem;
  margin: 0;
  text-align: center;
}

#description {
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  font-family: "Source Sans Pro";
}

.inputwrap {
  margin-top: 24px;
}

.col {
  display: flex;
  flex-direction: column;
}

.mt8 {
  margin-top: 8px;
}

input,
select {
  padding: 16px;
  font-size: 14px;
}

textarea {
  width: 100%;
}

button {
  margin-top: 32px;
  padding: 15px;
  border: 0;
  border-radius: 4px;
  font-size: 14px;
  color: white;
  background-color: #0063e6;
}

button:hover {
  background-color: #f0f6ff;
  cursor: pointer;
}
